// 
import { FC,useEffect,useState,useRef } from "react"
import { Link, useNavigate } from "react-router-dom"
import {ExclamationCircleOutlined} from '@ant-design/icons'
import { Modal, message,Popover,Switch} from 'antd'
import axios from "axios"
import 'swiper/swiper-bundle.css'
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, Navigation, Pagination } from 'swiper'
import './index.css'
const Application:FC = ()=>{
    const nav = useNavigate()
    const [username,setUsername] = useState('')
    const [open,setOpen] = useState(false)
    // modal
    const [modal_logout, contextHolder] = Modal.useModal()
    // modal
    const [modal_unregister, contextHolders] = Modal.useModal()
    // showmodal
    // ,
    const showmodal_logout = () => {
        modal_logout.confirm({
        title: '',
        icon: <ExclamationCircleOutlined />,
        content: '',
        okText: '',
        cancelText: '',
        onOk:()=>{
            logout()
        },
        })
    }
    // ,
    const showmodal_unregister = () => {
        setOpen(false)
        modal_unregister.confirm({
        title: '',
        icon: <ExclamationCircleOutlined />,
        content: ',!',
        okText: '',
        cancelText: '',
        onOk:()=>{
            unregister()
        },
        })
    }
    // popover
    const content = (
        <div>
            <Link to="/Personal" className="personal"></Link>
            <div style={{marginTop:'5px'}}>
                <div onClick={showmodal_logout} className="unregister"></div>
                {contextHolder}
            </div>
            <div style={{marginTop:'5px'}}>
                <div className="unregister" onClick={showmodal_unregister}></div>
                {contextHolders}
            </div>
        </div>
    )
    const handleOpenChange = (newOpen: boolean) => {
        setOpen(newOpen)
    }
    // axios
    useEffect(()=>{
        axios.get('/api/user/index')
        .then((res)=>{
            // code
            const code = res.data.data.code
            if(code === 1){
                const name = res.data.data.username
                // 
                setUsername(name)
                // setLog(true)
            }
        })
    },[])
    // 
    const unregister = ()=>{
        axios.post('/api/user/unregister')
        .then(()=>{
            // setLog(false)
            setUsername('')
            message.success('')
            nav('/')
        })
    }
    // 
    const logout = () =>{
        axios.post('/api/user/logout')
        .then(()=>{
            // setLog(false)
            setUsername('')
            message.success('')
            nav('/')
        })
    }
    // 
    const jumptoindex = ()=>{
        nav('/')
    }
    // 3
    const data = [
        {
            id:1,
            name:"",
            img:"./lvyou.svg",
            description:""
        },
        {
            id:2,
            name:"",
            img:"./biaoti.svg",
            description:""
        },
        {
            id:3,
            name:"",
            img:"./jiaolian.svg",
            description:""
        },
        {
            id:4,
            name:"",
            img:"./qiming.svg",
            description:""
        },
        {
            id:5,
            name:"",
            img:"./zhuangrong.svg",
            description:""
        },
        {
            id:6,
            name:"",
            img:"./jianli.svg",
            description:""
        },
        {
            id:7,
            name:"",
            img:"./mianshi.svg",
            description:""
        },
        {
            id:8,
            name:"",
            img:"./chuangye.svg",
            description:"ideaidea"
        },
        {
            id:9,
            name:"",
            img:"./qiuzhi.svg",
            description:""
        },
        {
            id:10,
            name:"",
            img:"./daoshi.svg",
            description:""
        },
        {
            id:11,
            name:"",
            img:"./more.svg",
            description:""
        }
    ]
    const jumptoapp = (id:number)=>{
        if(id === 11){
            message.warning(',')
        }else{
            nav(`/Apps/${id}`)
        }
    }
    return (
        <div>
            <header className={"header-area header-sticky background-header"}>
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <nav className="main-nav" style={{margin:'0 3%'}}>
                                <div id="nav_productName" onClick={jumptoindex}>
                                    <img id="nav_image" style={{width:'45px'}} src="./nav.png" alt="nav"/>
                                    <div style={{marginLeft:'10px'}}>
                                        <div style={{color:'#7453FC',fontStyle:'italic'}}>
                                            Better Prompt
                                        </div>
                                        <div style={{ fontStyle:'italic',fontSize:'15px'}}>
                                            Faster and more efficient
                                        </div>
                                    </div>
                                </div>
                                <ul className="nav">
                                    <li>
                                        <Link to='/'></Link>
                                    </li>
                                    <li>
                                        <Link to='/Home'></Link>
                                    </li>
                                    <li>
                                        <Link to='/Chat'></Link>
                                    </li>
                                    <li>
                                        <Link to='/Application' className="active"></Link>
                                    </li>
                                    <li>
                                        <Link to='/Author'></Link>
                                    </li>
                                    <li>
                                        <Popover content={content} open={open} trigger="hover" onOpenChange={handleOpenChange}>
                                            <a onClick={(e) => e.preventDefault()}>
                                                {username}
                                            </a>
                                        </Popover>
                                    </li>
                                </ul>   
                            </nav>
                        </div>
                    </div>
                </div>
            </header>
            <div className="page-heading" style={{paddingTop:'5%'}}>
                <div className="col-lg-12" style={{marginTop:'40px'}}>
                    <h6>Try Our Best Application</h6> 
                </div>
                <div id="certify" style={{top:"20px"}}>
                    <div className="swiper-container">
                            <Swiper
                                            
                                            key={data.length}
                                            className="swiper-wrapper"
                                            loop={true} //
                                            autoplay={true}
                                            watchSlidesProgress={true}  //Progressswiperprogress slideprogress
                                            loopedSlides={5} //slide3loopedSlides5
                                            slidesPerView={'auto'}
                                            pagination={{ clickable: true }} //trueSwiper
                                            navigation={true} //
                                            centeredSlides={true} //trueactive slide
                                            initialSlide={Math.floor(data.length/2)} //centeredSlide
                                            modules={[Navigation,Pagination,Autoplay]}
                                            // onProgress={onProgress} //SwiperprogressswiperSwiperprogress0-1
                                            // onSetTransition={onSetTransition} //SwipertranstionSwiperspeed
                                            >
                                        {data.map(t => {
                                            return (
                                                <SwiperSlide key={t.id} >
                                                    <div className="menu-item" onClick={(e)=>jumptoapp(t.id)}>
                                                        <div className="menu-item-box"></div>
                                                        <div className="menu-item-img">
                                                            <img src={t.img} alt={t.name} />
                                                        </div>
                                                        <div className="menu-item-names">
                                                            <h2 style={{fontStyle:'italic'}}>{t.name}</h2>
                                                            <p style={{fontStyle:'italic'}}>{t.description}</p>
                                                        </div>
                                                    </div>
                                                </SwiperSlide>
                                            )
                                        })}
                            </Swiper>
                    </div>
                </div>


            </div>



        </div>
    )
}
export default Application