// 
import { Link, useNavigate } from "react-router-dom"
import { Form,message,Popover } from 'antd'
import {FC} from "react"
import ParticlesBg from 'particles-bg'
import axios from 'axios'
import "./index.css"
const Login:FC = () => {
    const nav = useNavigate()
    // 
    const onfinish = (values: string)=>{
        console.log('Received values of form: ', values)
        // 
        axios.post('/api/user/login',{data:JSON.stringify(values)})
        .then((res)=>{
            // code
            const code = res.data.data.data
            console.log(code)
            switch(code){
                case 1:
                    message.success('!')
                    nav('/')
                    break;
                case 0:
                    message.warning('!')
                    break;
                default:
                    message.error('!')
            }
        })
    }
    return (
        <div className="log-in">
            {/*  */}
            {/* <div className="backs">
                <Link to="/" className="gobacks">
                    <img className="home" src="./home.svg" alt="svg1" />
                    
                </Link>
            </div> */}
            <div className="login-in">
                <ParticlesBg type="lines" bg={true} />
                {/*  */}
                <div className = "head">
                    <div >
                        <img src="./nav.png" alt="nav" style={{width:'50px',height:'50px',marginTop:'10px',marginLeft:'20px'}}/>
                    </div>
                    <h1>Better Prompt</h1>
                </div>
                {/*  */}
                <div className='login-in-form'> 
                    <div style={{color:'#fafafc',fontSize:'26px',margin:'10px'}}>
                        
                    </div>
                    {/* Form */}
                    <Form
                        name="normal_login"
                        className="login-form"
                        initialValues={{ remember: true }}
                        onFinish={onfinish}
                        >
                        {/*  item*/}
                        <Form.Item name="Username">
                            <Form.Item name="Username">
                                <Popover 
                                        content={<div style={{
                                            width: '90px',
                                            fontSize: '15px',
                                            lineHeight: '15px',
                                            color: '#757575',
                                        }}></div>}
                                        title={null}
                                        placement="leftTop"
                                        trigger="hover"
                                    >
                                    <input type="text" required className='input'/>
                                    <span className='span-username'>Username</span> 
                                </Popover>  
                            </Form.Item>
                        </Form.Item>
                        {/*  item*/}
                        <Form.Item name="Password" >
                            <Form.Item name="Password" >
                                <Popover 
                                            content={<div style={{
                                                width: '75px',
                                                fontSize: '15px',
                                                lineHeight: '15px',
                                                color: '#757575',
                                            }}></div>}
                                            title={null}
                                            placement="leftTop"
                                            trigger="hover"
                                        >
                                    <input type="password" required className='input-password' />
                                    <span className='span-password'>Password</span>
                                </Popover> 
                            </Form.Item>
                        </Form.Item>
                        {/*  */}
                        <Form.Item>
                            <Link to="/Register" className="link">?</Link>
                        </Form.Item>
                        {/*  */}
                        <Form.Item>
                            <button type="submit" className="button" >
                            
                            </button>
                        </Form.Item>
                    </Form>
                </div>
            </div>  
        </div>
    )
}
export default Login