// 
import { FC,useEffect,useState,useRef } from "react"
import { Link, useNavigate, useLocation } from "react-router-dom"
import {ExclamationCircleOutlined} from '@ant-design/icons'
import { Modal, message,Popover } from 'antd'
import axios from "axios"
import 'swiper/swiper-bundle.css'
import copy from 'copy-to-clipboard'
import ReactMarkdown from 'react-markdown'
import remarkGfm from 'remark-gfm'
import './index.css'
type Message = {
    content: string
    sender: 'user' | 'assistant'
    timestamp: string
    isfavorite: boolean
}
const Apps:FC = ()=>{
    // 
    const location = useLocation()
    // id
    const id = Number(location.pathname.split('/')[2]) - 1
    const nav = useNavigate()
    const [username,setUsername] = useState('')
    const [open,setOpen] = useState(false)
    // modal
    const [modal_logout, contextHolder] = Modal.useModal()
    // modal
    const [modal_unregister, contextHolders] = Modal.useModal()
    // showmodal
    // ,
    const showmodal_logout = () => {
        modal_logout.confirm({
        title: '',
        icon: <ExclamationCircleOutlined />,
        content: '',
        okText: '',
        cancelText: '',
        onOk:()=>{
            logout()
        },
        })
    }
    // ,
    const showmodal_unregister = () => {
        setOpen(false)
        modal_unregister.confirm({
        title: '',
        icon: <ExclamationCircleOutlined />,
        content: ',!',
        okText: '',
        cancelText: '',
        onOk:()=>{
            unregister()
        },
        })
    }
    // popover
    const content = (
        <div>
            <Link to="/Personal" className="personal"></Link>
            <div style={{marginTop:'5px'}}>
                <div onClick={showmodal_logout} className="unregister"></div>
                {contextHolder}
            </div>
            <div style={{marginTop:'5px'}}>
                <div className="unregister" onClick={showmodal_unregister}></div>
                {contextHolders}
            </div>
        </div>
    )
    const handleOpenChange = (newOpen: boolean) => {
        setOpen(newOpen)
    }
    // axios
    useEffect(()=>{
        axios.get('/api/user/index')
        .then((res)=>{
            // code
            const code = res.data.data.code
            if(code === 1){
                const name = res.data.data.username
                // 
                setUsername(name)
                // setLog(true)
            }
        })
    },[])
    // 
    const unregister = ()=>{
        axios.post('/api/user/unregister')
        .then(()=>{
            // setLog(false)
            setUsername('')
            message.success('')
            nav('/')
        })
    }
    // 
    const logout = () =>{
        axios.post('/api/user/logout')
        .then(()=>{
            // setLog(false)
            setUsername('')
            message.success('')
            nav('/')
        })
    }
    // 
    const jumptoindex = ()=>{
        nav('/')
    }
    const data = [
        {
            id:1,
            name:"",
            img:"./lvyou1.svg",
            message:'',
            description:"&2000&&&"  
        },
        {
            id:2,
            name:"",
            img:"./biaoti1.svg",
            message:'\n:\n&&52927272910185151152955 ',
            description:""
        },
        {
            id:3,
            name:"",
            img:"./jiaolian1.svg",
            message:'//',
            description:"180cm&90kg&&&3"
        },
        {
            id:4,
            name:"",
            img:"./qiming1.svg",
            message:'',
            description:"&&2022&6&10"
        },
        {
            id:5,
            name:"",
            img:"./zhuangrong1.svg",
            message:'',
            description:"&"
        },
        {
            id:6,
            name:"",
            img:"./jianli1.svg",
            message:'\n:\n\n'+
            '\n'+
            '123456789 123456789 123456789@example.com\n'+
            '\n'+
            '\n'+
            '\n'+
            '\n'+
            '\n'+
            '20206\n'+
            '\n'+
            '\n'+
            '\n'+
            '\n'+
            '20206 - 202112\n'+
            '\n'+
            '\n'+
            '- 20216\n'+
            '- 202012\n'+
            '\n'+
            'SQLPython\n'+
            'CFAPython\n'+
            '\n'+
            '\n'+
            '/\n'+
            '\n'+
            '\n'+
            '\n'+
            '\n',
            description:""
        },
        {
            id:7,
            name:"",
            img:"./mianshi1.svg",
            message:'',
            description:"C++"
        },
        {
            id:8,
            name:"",
            img:"./chuangye1.svg",
            message:'idea',
            description:""
        },
        {
            id:9,
            name:"",
            img:"./qiuzhi1.svg",
            message:'\n:\n&&',
            description:""
        },
        {
            id:10,
            name:"",
            img:"./daoshi1.svg",
            message:'',
            description:""
        }
    ]
    const [messages, setMessages] = useState<Message[]>([{
        content:data[id].message,
        sender:'assistant',
        timestamp:new Date().toLocaleString(),
        isfavorite:false
    }])
    const [isLoading, setIsLoading] = useState(false)// isLoading
    const [textareaValue, setTextareaValue] = useState('')
    const messageRef = useRef<HTMLDivElement>(null)
    const handelSendmessage = ()=>{
        if(textareaValue.length === 0){
            message.warning("~")
        }
        else if (messages.length >= 3){
            message.warning(",~")
        }
        else{
            const newMessage: Message = {
                content: textareaValue,
                sender: 'user',
                timestamp: new Date().toLocaleString(),
                isfavorite:false
            }
            setMessages([...messages, newMessage])
            // isLoadingtrue
            setTextareaValue('')
            setIsLoading(true)
            axios.post('/api/user/best_chat',{data:{text:JSON.stringify(textareaValue),code:id+1}})
                .then((res)=>{
                    console.log(res)    
                    const text = res.data.data.message
                    const replyMessage: Message = {
                        content: text,
                        sender: "assistant",
                        timestamp: new Date().toLocaleString(),
                        isfavorite:false
                        };
                    setMessages((prevMessages) => [...prevMessages, replyMessage]);
                    setIsLoading(false);
             })
        }
    }
    const copyanwser = (text:string)=>{
        copy(text)
        message.success("!")
    }
    const newchat = ()=>{
        const newMessage: Message = {
            content: data[id].message,
            sender: 'assistant',
            timestamp: new Date().toLocaleString(),
            isfavorite:false
        }
        setMessages([newMessage])
    }
    useEffect(() => {
        if (messageRef.current) {
          messageRef.current.scrollIntoView({ behavior: 'smooth' });
        }
    }, [messages])
    const renderers = {
        list: (props:any) => <ul>{props.children}</ul>,
        listItem: (props:any) => <li>{props.children}</li>,
      };
    return (
        <div>
            <header className={"header-area header-sticky background-header"}>
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <nav className="main-nav" style={{margin:'0 3%'}}>
                                <div id="nav_productName" onClick={jumptoindex}>
                                    <img id="nav_image" style={{width:'45px'}} src="./nav.png" alt="nav"/>
                                    <div style={{marginLeft:'10px'}}>
                                        <div style={{color:'#7453FC',fontStyle:'italic'}}>
                                            Better Prompt
                                        </div>
                                        <div style={{ fontStyle:'italic',fontSize:'15px'}}>
                                            Faster and more efficient
                                        </div>
                                    </div>
                                </div>
                                <ul className="nav">
                                    <li>
                                        <Link to='/'></Link>
                                    </li>
                                    <li>
                                        <Link to='/Home'></Link>
                                    </li>
                                    <li>
                                        <Link to='/Chat'></Link>
                                    </li>
                                    <li>
                                        <Link to='/Application' className="active"></Link>
                                    </li>
                                    <li>
                                        <Link to='/Author'></Link>
                                    </li>
                                    <li>
                                        <Popover content={content} open={open} trigger="hover" onOpenChange={handleOpenChange}>
                                            <a onClick={(e) => e.preventDefault()}>
                                                {username}
                                            </a>
                                        </Popover>
                                    </li>
                                </ul>   
                            </nav>
                        </div>
                    </div>
                </div>
            </header>
            <div className="page-heading" style={{paddingTop:'5%'}}>
                <div className="col-lg-13">
                    <div style={{display:'flex',margin:'auto'}}>
                        <div className="apps" onClick={(e)=>{nav('/Application')}}> &gt; </div>
                        <div>{data[id].name}</div>
                    </div>
                </div>
                <div className="box" style={{marginTop:'10px',backgroundColor:'#f4eded',color:'black'}}>
                    <div className="box-left" style={{backgroundColor:'rgb(255 255 255 / 87%)'}}>
                        <div className="box-left-top">
                            <div className="box-letf-top-title">
                                Better Prompt
                            </div>
                            <div className="box-letf-top-title2">
                                Faster and more efficient.
                            </div>
                            <div className="box-icon">
                                <img src="./nav.png" alt="icon"  />
                            </div>
                        </div>
                        <div>
                            <div style={{display:'block',letterSpacing:'2px',alignItems:'center',justifyContent:'space-between',marginTop:'5%'}}>
                                <div className="rule">
                                    
                                </div>
                                <div className="text" style={{marginTop:'10%',fontSize:'16px',fontStyle:'italic'}}>
                                    !Better Prompt <span style={{color:'#f10d95'}}>{data[id].name}</span>,
                                    ,~<br />
                                    
                                </div>
                                <div className="rule">
                                    Examples:
                                </div>
                                <div className="text">
                                    {data[id].description}
                                </div>

                            </div>
                        </div>
                        <div className="box-left-bottom" style={{paddingTop:'60%'}}>
                            <div style={{display:'inline-flex'}}>
                                <div className="address">
                                    <a target="_blank" rel="noopener noreferrer" href="https://github.com/reatcat/baidu_api">
                                        <button className="box-button" style={{backgroundColor:'#262828'}}>
                                            <div className="address-icon">
                                                <img src="./address.svg" alt="address" />
                                            </div>
                                        </button>
                                    </a>
                                </div>
                            </div>
                            <div>
                                <button className="box-button" onClick={(e)=>newchat()} style={{backgroundColor:'#262828'}}>
                                    <div className="box-button-icon">
                                        <img src="./add.svg" alt="" />
                                    </div>
                                    <div className="newchat">
                                        
                                    </div>
                                </button>
                            </div>
                        </div>
                    </div>
                    <div className="box-right">
                        <div className="box-right-box">
                            <div className="box-chat">
                                {messages.map((message, index) => (
                                    <div
                                        key={index}
                                        className={`message ${message.sender === 'user' ? 'user' : 'assistant'}`}
                                        ref={index === messages.length - 1 ? messageRef : null}
                                    >
                                        <div className="message-container" >
                                            <div style={{marginTop:'20px'}}>
                                                <div className="user-avatar" style={{boxShadow:'0px 2px 4px 0px rgb(22 21 21 / 62%)'}}>
                                                    <img style={{width:'25px',height:'25px'}} src={message.sender === 'user' ?"./user2.svg":data[id].img} alt="avatar" />
                                                </div>
                                            </div>
                                            <div className="message-item" style={{backgroundColor:'rgb(22 21 21 / 45%)',color:'#2d2c2c'}}>
                                                {
                                                    message.sender === 'user'?
                                                    <></>
                                                    :
                                                    index === 0?
                                                    <></>
                                                    :
                                                    <div className="actions">
                                                        <div className="action2" onClick={(e)=>copyanwser(message.content)}>
                                                            
                                                        </div>
                                                    </div>
                                                }
                                                <div className="message-body" style={{whiteSpace:'pre-line',color:'#2d2c2c'}}>
                                                    <ReactMarkdown remarkPlugins={[remarkGfm]} className="markdown">{message.content}</ReactMarkdown>
                                                </div>
                                            </div>
                                            <div className="message-time">
                                                <div >
                                                    {message.timestamp} 
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                ))}
                                {isLoading && (
                                    <div className="message assistant">
                                        <div className="message-container">
                                            <div style={{marginTop:'20px'}}>
                                                <div className="user-avatar">
                                                    <img style={{width:'25px',height:'25px'}} src={data[id].img} alt="avatar" />
                                                </div>
                                            </div>
                                            <div className="message-item" style={{backgroundColor:'rgb(22 21 21 / 45%)',color:'#fff'}}>
                                                <div className="message-body" style={{whiteSpace:'pre-line',color:'#fff'}}>
                                                    <div className="loading">
                                                        <div></div>
                                                        <div></div>
                                                        <div></div>
                                                    </div>
                                                   
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                )}
                            </div>
                            <div className="box-right-input">
                                <div className="box-right-input-bottom">
                                    <textarea value={textareaValue} className="send-question" style={{backgroundColor:'white',color:'#2d2c2c'}} placeholder="Ctrl + Enter" 
                                    onChange={(e) => setTextareaValue(e.target.value)}
                                    onKeyDown={(e) =>{if (e.ctrlKey && e.key === 'Enter') {
                                        e.preventDefault()
                                        handelSendmessage()
                                    }} }
                                    rows={3}></textarea>
                                    <button className="send-button" onClick={handelSendmessage}>
                                        <div className="send-button-icon">
                                            <img src="./send.svg" alt="send" />
                                        </div>
                                        <div className="send">
                                            
                                        </div>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Apps